<template>
  <div id="delivery">
    <!-- 搜索框 -->
    <van-sticky>
      <van-row class="search-model">
        <van-col span="3" class="back-col" @click="back">
          <van-icon name="arrow-left" color="white" class="back-btn" />
        </van-col>
        <van-col span="21">
          <van-search
            placeholder="请输入姓名搜索"
            background="#6e7bd8"
            show-action
            v-model="value">
            <div slot="left-icon"></div>
            <div slot="action" @click="onSearch">
              <span class="search-btn">
                搜索
              </span>
            </div>
          </van-search>
        </van-col>
      </van-row>
    </van-sticky>

    <!-- 快递列表 -->
    <transition name="fade">
      <van-list
        key="list-key"
        class="list-model"
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div v-for="item in list" :key="item" class="list-item van-hairline--bottom">
          <van-row class="list-item-row">
            <van-col span="6" class="list-item-num">
              2019-12-10
            </van-col>
            <van-col span="3">
              快递
            </van-col>
            <van-col span="12" class="list-item-num">
              753187166753187
            </van-col>
            <van-col span="3">
              姓名
            </van-col>
          </van-row>
        </div>
      </van-list>
    </transition>
  </div>
</template>

<script>

export default {
  name: 'delivery',
  data () {
    return {
      value: '',
      list: [],
      loading: false,
      finished: false
    }
  },
  methods: {
    back () {
      this.$router.push('/')
    },
    onSearch () { },
    onLoad () {
      // 异步更新数据
      setTimeout(() => {
        for (let i = 0; i < 15; i++) {
          this.list.push(this.list.length + 1)
        }
        // 加载状态结束
        this.loading = false

        // 数据全部加载完成
        if (this.list.length >= 45) {
          this.finished = true
        }
      }, 500)
    }
  }
}
</script>

<style lang="less">
#delivery {
  font-size: 14px;
  .search-model {
    background: #6e7bd8;
    .back-col {
      height: 51.8px;
      line-height: 51.8px;
      .back-btn {
        vertical-align: middle;
      }
    }
    .van-search--show-action {
      padding-left: 0;
    }
    .search-btn {
      padding: 0 2px;
      color: white;
    }
  }

  .list-model {
    background: white;
    .list-item-row {
      padding: 15px 0;
      .list-item-num {
        padding: 0 5px;
        line-height: 20px;
        word-break: break-all;
        word-wrap: break-word;
      }
    }
  }
}
.fade-enter-active {
  transition: opacity .5s;
}
.fade-enter {
  opacity: 0;
}
</style>
